html {
  font-size: 18px; }

body {
  background-color: #f2f2f2;
  font-family: "Segoe UI", sans-seriif;
  font-size: 1rem;
  font-weight: lighter;
  line-height: 1.5rem;
  overflow-y: scroll;
  margin: 0; }

h1, h2, h3, h4, h5 {
  margin: 0.5rem 0 0.5rem 0;
  font-weight: 300;
  line-height: 1.5em; }

h2 {
  margin-top: 2rem; }

p {
  margin: 0 0 0.5rem 0; }

input,
select,
button,
textarea {
  color: #222;
  padding: 0 0.2rem;
  border: 1px solid #b0b0b0;
  vertical-align: middle;
  white-space: normal;
  font-size: 1rem;
  font-family: "Segoe UI", sans-seriif;
  font-weight: lighter; }
  input:focus,
  select:focus,
  button:focus,
  textarea:focus {
    outline: 0; }

button {
  padding: 0.5rem;
  cursor: pointer; }

a {
  color: #5AAF41; }
  a:hover {
    color: #478a33; }

main {
  margin: auto;
  max-width: 32.5rem;
  padding: 1rem 2rem; }

form label {
  display: inline-block;
  width: 20%; }

form textarea {
  box-sizing: border-box;
  width: 100%;
  height: 10rem; }

form input {
  width: 77%; }

a {
  display: inline-block;
  position: relative;
  text-decoration: none; }
  a:after, a:before {
    border-bottom: 1px solid #478a33;
    bottom: 0;
    content: "";
    display: inline-block;
    height: 2px;
    left: 0;
    position: absolute;
    transition: all 0.15s ease-out;
    width: 0; }
  a:before {
    border-bottom-color: #5AAF41;
    border-bottom-style: dashed;
    width: 100%; }
  a:hover:after {
    width: 100%; }

.button {
  background-color: #5AAF41;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  padding: 0.5rem; }
  .button:hover {
    background-color: #478a33; }

form {
  background-color: rgba(51, 51, 51, 0.8);
  height: 0vh;
  opacity: 0;
  overflow-y: scroll;
  overflow-x: hidden;
  position: fixed;
  left: 0;
  top: 0;
  transition: 0.25s opacity ease-in;
  width: 100vw;
  z-index: 9990;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center; }
  form fieldset {
    border: 0;
    background-color: #f2f2f2;
    display: block;
    max-width: 32.5rem;
    overflow: hidden;
    padding: 1rem;
    position: absolute;
    width: 100%; }
    form fieldset h2 {
      margin-top: 0; }

#calltoaction {
  cursor: pointer;
  margin: 0;
  opacity: 0;
  position: fixed;
  height: 0;
  right: 1rem;
  top: 1rem;
  transition: 0.25s opacity ease-in;
  z-index: 9999; }
  #calltoaction:after, #calltoaction:before {
    background: #f2f2f2;
    content: "";
    display: block;
    height: 2px;
    position: absolute;
    width: 1rem;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); }
  #calltoaction:before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); }
  #calltoaction:checked {
    opacity: 1; }
    #calltoaction:checked ~ main {
      height: calc(100vh - 2rem);
      overflow: hidden; }
    #calltoaction:checked + main form {
      height: 100vh;
      opacity: 1; }

.card {
  color: #222; }

* {
  transition: all 0.25s ease-in-out; }
